.image-map {
    background-image: url("../../../../../Data/sites/1/media/lightrail/metro_map_horizontalstripmap.png");
    width: 750px;
    height: 166px;
    repeat: no-repeat;
    position: relative;

    .rectangle {
        position: absolute;
        width: 20px;
        height: 90px;
        transform: rotate(45deg);
        color: transparent;
    }

    .dot {
        position: absolute;
        width: 25px;
        height: 20px;
        transform: rotate(45deg);
    }

